body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 960px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 960px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.debug > div > div > div {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(8.333333333333334% - 10px);
  height: inherit;
  outline: 1px dotted #a69ed4;
  background: rgba(0, 0, 0, 0.1);
}

.content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
}

.content .box {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
}

.content .green {
  background: green;
  height: 100px;
}

.content .blue {
  background: blue;
  height: 100px;
}

.content .yellow {
  background: yellow;
  height: 100px;
}

.items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.items .item {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.items .item .img {
  border-radius: 50%;
}

.items .item .subtitle {
  color: #fff;
  font-size: 1.5rem;
  margin: 3px 0;
}

.items .item .text {
  font-size: 16px;
  color: #d94948;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }

  .debug > div {
    padding-left: 30px;
    padding-right: 30px;
  }

  .content .green {
    background: #4704ff;
    padding: 20px;
  }
}

@media screen and (max-width: 768px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .debug > div {
    padding-left: 15px;
    padding-right: 15px;
  }

  .debug > div {
    padding-left: 15px;
    padding-right: 15px;
  }

  .content .box {
    width: calc(50% - 10px);
  }

  .content .box {
    width: calc(91.66666666666667% - 10px);
  }

  .content .green {
    background: pink;
    padding: 10px;
  }

  .items .item {
    width: calc(50% - 10px);
  }

  .items .item {
    background: yellow;
    padding: 10px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }

  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }

  .items .item {
    width: calc(100% - 10px);
  }
}

@media screen and (max-width: 420px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }

  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }

  .content .green {
    background: red;
    padding: 10px;
  }

  .items .item {
    background: red;
    padding: 10px;
  }
}